import Vue from 'vue'
import Vuex from 'vuex'
// modules
import auth from './modules/auth'

// api
import {getNews, getCity} from '../../service/index'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    auth
  },
  getters: {
    Cities: () => {
      return getCity()
    },
    News: (state) => {
      return state.news
    },
    PageLock: (state) => {
      return state.pageLock
    }
  },
  state: {
    news: [],
    pageLock: false,
  },
  mutations: {
    updateNews: (state, payload) => {
      state.news = payload
    },
    updatePageLock: (state, payload) => {
      state.pageLock = payload
    }
  },
  actions: {
    setPageLock ({commit}, data) {
      commit('updatePageLock', data)
    },
    async loadNews ({ commit }) {
      const data = await getNews()
      commit('updateNews', data)
    }
  }
})
