<script>
import {commonMixin} from '../mixins/common'
import {mapActions, mapGetters} from 'vuex'
import DemoModal from './components/DemoModal'

export default {
  name: 'App',
  mixins: [commonMixin],
  computed: {
    ...mapGetters(['BaseUrl'])
  },
  components: {DemoModal},
  data () {
    return {
      table: null,
      demoModalOpen: false,
      selected: ''
    }
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),
    toggleMessage (bool) {
      this.messageMore = bool
      return;
    },
    fetchData() {
      this.setPageLock(false)
      setTimeout(() => {
        this.$('html, body').scrollTop(0)
        // document.scrollTop = 0
        // document.body.scrollTop = 0
        // document.scrollingElement.scrollTop = 0
      }, 250)
    }
  },
  mounted () {
    // header 縮放
    document.body.onscroll = () => {
      let scroll = document.scrollTop || document.body.scrollTop || document.scrollingElement.scrollTop || 0;
      let header = this.$el.querySelector('header');

      if (scroll > 70) {
        header.classList.add('compact')
      } else {
        header.classList.remove('compact')
      }
    }
    
    this.table = window.$('#datatable').DataTable({
      paging: false,
      searching: false
    })
  }
}
</script>
